.mdui-fab[disabled],
.mdui-btn-raised[disabled]:hover,
.mdui-fab[disabled]:hover,
.mdui-btn-raised[disabled]:active,
.mdui-fab[disabled]:active,
.mdui-btn-raised[disabled]:focus,
.mdui-fab[disabled]:focus {
  background-color: rgba(0, 0, 0, 0.12) !important;
  box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12) !important;
}
/* 加粗按钮文本 */
.mdui-btn-bold {
  font-weight: bold;
}
/* 图标按钮 */
.mdui-btn-icon {
  width: 36px;
  min-width: 36px;
  height: 36px;
  margin-right: 0;
  margin-left: 0;
  padding: 0;
  overflow: hidden;
  font-size: 24px;
  line-height: normal;
  border-radius: 50%;
}
.mdui-btn-icon .mdui-icon {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 24px;
  line-height: 24px;
  transform: translate(-12px, -12px);
}
.mdui-btn-icon.mdui-ripple {
  transform: translateZ(0);
}
/* 按钮 100% 宽度 */
.mdui-btn-block {
  display: block;
  width: 100%;
}
/* 密集型按钮 */
.mdui-btn-dense {
  height: 32px;
  font-size: 13px;
  line-height: 32px;
}
.mdui-btn-dense.mdui-btn-icon {
  width: 32px;
  min-width: 32px;
}
/* 按钮组 */
.mdui-btn-group {
  position: relative;
  display: inline-block;
  vertical-align: middle;
}
.mdui-btn-group .mdui-btn {
  float: left;
  min-width: inherit;
  padding: 0 12px;
  color: rgba(0, 0, 0, 0.54);
  border-radius: 0;
}
.mdui-btn-group .mdui-btn::before {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  border-left: 1px solid transparent;
  content: ' ';
}
.mdui-btn-group .mdui-btn:first-child {
  border-top-left-radius: 2px;
  border-bottom-left-radius: 2px;
}
.mdui-btn-group .mdui-btn:first-child::before {
  border-left: none;
}
.mdui-btn-group .mdui-btn:last-child {
  border-top-right-radius: 2px;
  border-bottom-right-radius: 2px;
}
.mdui-btn-group .mdui-btn.mdui-btn-active {
  color: rgba(0, 0, 0, 0.87);
  background-color: rgba(0, 0, 0, 0.215);
}
.mdui-btn-group .mdui-btn.mdui-btn-active + .mdui-btn-active::before {
  border-left: 1px solid rgba(0, 0, 0, 0.145);
}
