  color: rgba(0, 0, 0, 0.87);
  font-size: 16px;
  font-family: inherit;
  line-height: 20px;
  background: none;
  border: none;
  border-bottom: 1px solid rgba(0, 0, 0, 0.42);
  border-radius: 0;
  outline: none;
  box-shadow: none;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
