/**
 * =============================================================================
 * ************   Dialog alert   ************
 * =============================================================================
 */
/**
 * =============================================================================
 * ************   Dialog 提示框   ************
 * =============================================================================
 */
.mdui-dialog {
  position: fixed;
  right: 0;
  left: 0;
  z-index: 6000;
  display: none;
  box-sizing: border-box;
  width: 92%;
  min-width: 180px;
  max-width: 728px;
  max-height: 90%;
  margin: auto;
  overflow: hidden;
  color: #000;
  background-color: #fff;
  border-radius: 2px;
  transform: scale(0.95);
  opacity: 0;
  transition-duration: 0.3s;
  transition-property: transform, opacity, visibility;
  will-change: top, opacity, transform;
  box-shadow: 0 11px 15px -7px rgba(0, 0, 0, 0.2), 0 24px 38px 3px rgba(0, 0, 0, 0.14), 0 9px 46px 8px rgba(0, 0, 0, 0.12);
}
@media (min-width: 600px) {
  .mdui-dialog {
    width: 85%;
    max-height: 85%;
  }
}
@media (min-width: 1024px) {
  .mdui-dialog {
    width: 80%;
    max-height: 80%;
  }
}
/* 打开提示框 */
.mdui-dialog-open {
  transform: scale(1);
  opacity: 1;
}
/* 提示框标题 */
.mdui-dialog-title {
  box-sizing: border-box;
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  text-align: left;
}
/* 标题固定在顶部 */
.mdui-dialog > .mdui-dialog-title {
  padding: 24px 24px 20px 24px;
}
/* 提示框内容 */
.mdui-dialog-content {
  box-sizing: border-box;
  padding: 24px;
  overflow-y: auto;
  color: rgba(0, 0, 0, 0.7);
  font-size: 15px;
  line-height: 1.5;
  -webkit-overflow-scrolling: touch;
}
/* 标题随内容滚动 */
.mdui-dialog-content > .mdui-dialog-title {
  padding-bottom: 20px;
}
.mdui-dialog-title + .mdui-dialog-content {
  padding-top: 0;
}
/* 提示框底部按钮 */
.mdui-dialog-actions {
  box-sizing: border-box;
  padding: 8px;
  text-align: right;
}
.mdui-dialog-actions .mdui-btn {
  min-width: 64px;
  margin-left: 8px;
  color: #E91E63;
}
.mdui-dialog-actions .mdui-btn:first-child {
  margin-left: 0;
}
/* 底部按钮滚随内容动 */
.mdui-dialog-content > .mdui-dialog-actions {
  margin: 0 -24px -24px -24px;
  padding-top: 32px;
}
/* 使提示框底部按钮竖排 */
.mdui-dialog-actions-stacked {
  padding: 8px 0 8px 0;
}
.mdui-dialog-actions-stacked .mdui-btn {
  width: 100%;
  height: 48px;
  margin: 0;
  line-height: 48px !important;
  text-align: right;
  border-radius: 0;
}
/**
 * =============================================================================
 * ************   Dialog 强调色   ************
 * =============================================================================
 */
/**
 * =============================================================================
 * ************   Dialog dark   ************
 * =============================================================================
 */
.mdui-dialog-alert {
  max-width: 448px;
}
/**
 * =============================================================================
 * ************   Dialog confirm   ************
 * =============================================================================
 */
.mdui-dialog-confirm {
  max-width: 448px;
}
/**
 * =============================================================================
 * ************   Dialog prompt   ************
 * =============================================================================
 */
.mdui-dialog-prompt {
  max-width: 448px;
}
.mdui-dialog-prompt .mdui-textfield {
  padding-top: 0;
}
/**
 * =============================================================================
 * ************   Toolbar 工具栏   ************
 * =============================================================================
 */
.mdui-toolbar {
  display: flex;
  align-items: center;
  box-sizing: border-box;
  width: 100%;
}
.mdui-toolbar > * {
  margin: 0 16px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.mdui-toolbar[class*="mdui-color-"]:not(.mdui-color-transparent) .mdui-btn:hover {
  background-color: rgba(255, 255, 255, 0.1);
}
.mdui-toolbar[class*="mdui-color-"]:not(.mdui-color-transparent) .mdui-btn:active {
  background-color: rgba(255, 255, 255, 0.165);
}
.mdui-toolbar > a {
  color: inherit;
  text-decoration: none;
  user-select: none;
}
.mdui-toolbar > .mdui-btn-icon {
  width: 48px;
  min-width: 48px;
  height: 48px;
}
@media (orientation: landscape) and (max-width: 959.9px) {
  .mdui-toolbar > .mdui-btn-icon {
    width: 40px;
    min-width: 40px;
    height: 40px;
  }
}
.mdui-toolbar > .mdui-btn-icon .mdui-icon {
  height: 24px;
  line-height: 24px;
}
.mdui-toolbar .mdui-icon {
  color: inherit;
}
.mdui-toolbar-spacer {
  flex-grow: 1;
  margin: 0;
}
/* 手机 */
.mdui-toolbar {
  height: 56px;
  line-height: 56px;
}
.mdui-toolbar > .mdui-btn {
  margin: 0 4px;
}
.mdui-toolbar > .mdui-btn + .mdui-btn {
  margin-left: 0;
}
@media (min-width: 600px) {
  .mdui-appbar .mdui-toolbar {
    height: 64px;
    line-height: 64px;
  }
  .mdui-appbar .mdui-toolbar > .mdui-btn {
    margin: 0 8px;
  }
  .mdui-appbar .mdui-toolbar > .mdui-btn + .mdui-btn {
    margin-left: 0;
  }
}
@media (orientation: landscape) and (max-width: 959.9px) {
  .mdui-appbar .mdui-toolbar {
    height: 48px;
    line-height: 48px;
  }
  .mdui-appbar .mdui-toolbar > .mdui-btn {
    margin: 0 4px;
  }
  .mdui-appbar .mdui-toolbar > .mdui-btn + .mdui-btn {
    margin-left: 0;
  }
}
/**
 * =============================================================================
 * ************   Icon 图标   ************
 * =============================================================================
 */
.mdui-icon,
.mdui-icon::before {
  color: inherit;
  font-weight: normal;
  font-size: 24px;
  /* Preferred icon size */
  font-style: normal;
  line-height: 1;
  direction: ltr;
  letter-spacing: normal;
  white-space: nowrap;
  text-transform: none;
  vertical-align: middle;
  word-wrap: normal;
}
.mdui-icon {
  display: inline-block;
  text-align: center;
}
.mdui-icon::before {
  display: block !important;
  width: 24px;
  height: 24px;
}
/**
 * =============================================================================
 * ************   Material Icons   ************
 * =============================================================================
 *
 * https://github.com/google/material-design-icons/
 * v3.0.1
 */
@font-face {
  font-weight: 400;
  font-family: 'Material Icons';
  font-style: normal;
  src: local('Material Icons'), local('MaterialIcons-Regular'), url(../icons/material-icons/MaterialIcons-Regular.woff2) format('woff2'), url(../icons/material-icons/MaterialIcons-Regular.woff) format('woff');
}
.material-icons {
  /* stylelint-disable-next-line */
  font-family: 'Material Icons';
  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;
  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;
  /* Support for IE. */
  font-feature-settings: 'liga';
}
/**
 * =============================================================================
 * ************   Typography 文章排版   ************
 * =============================================================================
 */
.mdui-typo {
  line-height: 1.8;
  word-wrap: break-word;
  /* a */
  /* small */
  /* blockquote */
  /* mark */
  /* h1 - h6 */
  /* code */
  /* pre code */
  /* abbr */
  /* ins */
  /* u */
  /* del */
  /* hr */
  /* pre */
  /* kbd */
  /* ul / ol */
  /* img, video */
}
.mdui-typo address,
.mdui-typo caption,
.mdui-typo cite,
.mdui-typo code,
.mdui-typo dfn,
.mdui-typo th {
  font-weight: 400;
  font-style: normal;
}
.mdui-typo caption,
.mdui-typo th {
  text-align: left;
}
.mdui-typo q::before,
.mdui-typo q::after {
  content: '';
}
.mdui-typo pre,
.mdui-typo code,
.mdui-typo kbd,
.mdui-typo samp,
.mdui-typo pre tt {
  font-family: Consolas, Courier, 'Courier New', monospace;
}
.mdui-typo figcaption {
  color: rgba(0, 0, 0, 0.54);
  font-size: 80%;
}
.mdui-typo [draggable],
.mdui-typo [draggable="true"] {
  cursor: move;
}
.mdui-typo [draggable="false"] {
  cursor: inherit;
}
.mdui-typo p,
.mdui-typo pre,
.mdui-typo ul,
.mdui-typo ol,
.mdui-typo dl,
.mdui-typo form,
.mdui-typo hr,
.mdui-typo figure,
.mdui-typo table,
.mdui-typo .mdui-table,
.mdui-typo .mdui-table-fluid {
  margin: 0 0 1.2em 0;
}
.mdui-typo p:last-child,
.mdui-typo pre:last-child,
.mdui-typo ul:last-child,
.mdui-typo ol:last-child,
.mdui-typo dl:last-child,
.mdui-typo form:last-child,
.mdui-typo hr:last-child,
.mdui-typo figure:last-child,
.mdui-typo table:last-child,
.mdui-typo .mdui-table:last-child,
.mdui-typo .mdui-table-fluid:last-child {
  margin-bottom: 0;
}
.mdui-typo a {
  color: #FF4081;
  position: relative;
  display: inline-block;
  overflow: hidden;
  text-decoration: none;
  vertical-align: top;
  outline: none;
}
.mdui-typo a::before {
  position: absolute;
  top: auto;
  bottom: 1px;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #FF4081;
  transform: scaleX(0);
  backface-visibility: hidden;
  transition: all 0.2s;
  content: ' ';
}
.mdui-typo a:hover::before,
.mdui-typo a:focus::before {
  transform: scaleX(1);
}
.mdui-typo small {
  font-size: 80%;
}
.mdui-typo blockquote {
  margin: 1em 3em 1em 2em;
  padding-left: 1em;
  font-weight: 400;
  border-left: 4px solid rgba(0, 0, 0, 0.12);
}
@media only screen and (max-width: 599.9px) {
  .mdui-typo blockquote {
    margin: 1em 0;
  }
}
.mdui-typo blockquote:last-child {
  margin-bottom: 0;
}
.mdui-typo blockquote footer {
  color: rgba(0, 0, 0, 0.54);
  font-size: 86%;
}
.mdui-typo mark {
  margin: 0 5px;
