}
.mdui-typo h1:last-child,
.mdui-typo h2:last-child,
.mdui-typo h3:last-child,
.mdui-typo h4:last-child,
.mdui-typo h5:last-child,
.mdui-typo h6:last-child {
  margin-bottom: 0;
}
.mdui-typo h1 small,
.mdui-typo h2 small,
.mdui-typo h3 small,
.mdui-typo h4 small,
.mdui-typo h5 small,
.mdui-typo h6 small {
  color: rgba(0, 0, 0, 0.54);
  font-weight: 400;
  font-size: 65%;
  line-height: 1;
}
.mdui-typo h1 {
  font-size: 2em;
}
.mdui-typo h2 {
  font-size: 1.8em;
}
.mdui-typo h3 {
  font-size: 1.6em;
}
.mdui-typo h4 {
  font-size: 1.4em;
}
.mdui-typo h5 {
  font-size: 1.2em;
}
.mdui-typo h6 {
  font-size: 1.1em;
}
.mdui-typo code {
  padding: 2px 6px;
  color: #c7254e;
  background-color: #f7f7f9;
  border-radius: 2px;
}
.mdui-typo pre code {
  padding: 0;
  color: inherit;
  font-size: inherit;
  line-height: 1.7;
  background-color: transparent;
  border-radius: 0;
}
.mdui-typo abbr[title] {
  text-decoration: none;
  border-bottom: 1px dotted;
  cursor: help;
}
.mdui-typo ins {
  text-decoration: none;
  border-bottom: 1px solid ;
}
.mdui-typo u {
  text-decoration: none;
  border-bottom: 1px solid;
}
.mdui-typo del {
  text-decoration: line-through;
}
.mdui-typo hr {
  height: 10px;
  margin-bottom: 0.8em;
  border: none;
  border-bottom: 1px solid rgba(0, 0, 0, 0.12);
}
.mdui-typo pre {
  padding: 12px 16px;
  overflow-x: auto;
  border: 1px solid rgba(0, 0, 0, 0.12);
  border-radius: 2px;
  -webkit-overflow-scrolling: touch;
}
.mdui-typo kbd {
  padding: 2px 6px;
  color: #fff;
  font-size: 90%;
  background-color: #333;
