}
.mdui-typo img,
.mdui-typo video {
  max-width: 100%;
}
.mdui-typo figure {
  text-align: center;
}
.mdui-typo figure figcaption {
  margin-top: 8px;
  color: #999;
  font-size: 14px;
}
.mdui-typo figure figcaption:empty::before {
